<template>
	<div class="setting-container">
		<div class="section__container">
			<div class="section__header">账户设置</div>
			<div class="section__body">
				<el-row type="flex" class="section__row-item" justify="space-between">
					<el-col :span="5">头像</el-col>
					<el-col :span="12"><img :src="userinfo.userImg" /></el-col>
					<el-col :span="5" class="col-item__button" @click.native="changeImg">修改</el-col>
				</el-row>
				<el-row type="flex" class="section__row-item" justify="space-between">
					<el-col :span="5">昵称</el-col>
					<el-col :span="12">{{userinfo.userName}}</el-col>
					<el-col :span="5" class="col-item__button" @click.native='changeName'>修改</el-col>
				</el-row>
				<el-row type="flex" class="section__row-item" justify="space-between">
					<el-col :span="5">邮箱</el-col>
					<el-col :span="12">{{userinfo.userEmail}}</el-col>
					<el-col :span="5" class="col-item__button" @click.native="changeEmail">修改</el-col>
				</el-row>
				<el-row type="flex" class="section__row-item" justify="space-between">
					<el-col :span="5">密码</el-col>
					<el-col :span="12">**********</el-col>
					<el-col :span="5" class="col-item__button" @click.native="changePassword">修改</el-col>
				</el-row>
				<el-row type="flex" class="section__row-item" justify="space-between">
					<el-col :span="5">手机号</el-col>
					<el-col :span="12">{{userinfo.userTelephone}}</el-col>
					<el-col :span="5" class="col-item__button" @click.native="changePhone">修改</el-col>
				</el-row>
				
			</div>
		</div>
	</div>
</template>

<script>
	import {
		getUserInfo,
		updateUserName,
		updateEmail,
		updatePsw,
		updateTelephone,
	} from "@/network/user.js";
	export default {
		name: 'Setting',
		data() {
			return {
				userinfo: ''
			}
		},
		methods: {
			//修改头像
			changeImg() {
				console.log('修改头像')
			},
			//修改昵称
			changeName() {
				this.$prompt("新的昵称", "修改昵称", {
					confirmButtonText: "保存",
					cancelButtonText: "取消",
				}).then(({ value }) => {
					let formData = new FormData();
					formData.append('userName', value)
					updateUserName(formData).then((res) => {
						console.log(res);
					})
				}).catch(() => {});
			},
			//修改邮箱
			changeEmail() {
				this.$prompt("请输入邮箱", "更改邮箱", {
					confirmButtonText: "确定",
					cancelButtonText: "取消",
					inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
					inputErrorMessage: "邮箱格式不正确",
				})
					.then(({ value }) => {
						updateEmail(value).then(res => {
							console.log(res)
							this.$message({
								type: "success",
								message: res.msg
							});
							this.userinfo.userEmail = value
						});
					}).catch(() => {})
			},
			//修改密码
			changePassword() {
				this.$prompt("请输入密码", "修改密码", {
					confirmButtonText: "确定",
					cancelButtonText: "取消",
				})
					.then(({ value }) => {
						updatePsw(value).then(res => {
							console.log(res);
							this.$message({
								type: "success",
								message: "你的psw是: " + value,
							});
						});
					}).catch(() => {})
			},
			//修改手机号
			changePhone() {
				this.$prompt("请输入手机号", "修改手机号", {
					confirmButtonText: "保存",
					cancelButtonText: "取消",
				}).then(({ value }) => {
						updateTelephone(value).then(res => {
							console.log(res)
							this.$message({
								type: "success",
								message: res.msg,
							});
							this.userinfo.userTelephone = value;
						});
					}).catch(() => {})
			}
		},
		created() {
			getUserInfo().then(res => {
				this.userinfo = res.data
			})
		}
	}
</script>

<style>
	.section__container {
		padding: 1rem;
	}
	.section__header {
		font-weight: 700;
		font-size: 1.5rem;
		margin-bottom: 1.5rem;
	}
	.section__row-item {
		padding: 1.5rem 0;
		border-top: 1px solid #dfdfdf;
	}
	.rsection__row-item:last-child {
		padding-bottom: 0;
	}
	.col-item__button {
		text-align: right;
		color: var(--color-text-change);
	}
	.col-item__button:hover {
		color: var(--color-btn-focus);
	}
</style>
